import React, { useState, useRef, useEffect } from 'react';
import Text from './Text';
import Player from './Player';
import SEO from './SEO';
import Link from './Link';
import { Link as ScrollLink } from 'react-scroll';
import Button from './Button';
import Icon from './Icon';
import { classdFn as classNames } from 'classd';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import InnerImageZoom from 'react-inner-image-zoom';
import styles from '@styles/components/detail.module.scss';
import playerStyles from '@styles/components/player.module.scss';
import 'react-inner-image-zoom/lib/InnerImageZoom/styles.css';


export default function Detail(props) {
  const {
    id,
    player: _player,
    data: _data,
  } = props;

  // console.log(props.data);

  // Get stuff from props
  const { projects, artists, setlist } = _data;
  const { player } = _player;

  // Build data from stuff
  const data = {
    id: id,
    project: { ...projects[id] },
    artists: [
      { 
        id: projects[id].artists[0],
        ...artists[projects[id].artists[0]]
      },
      { 
        id: projects[id].artists[1],
        ...artists[projects[id].artists[1]]
      },
    ],
    assets: props.data.assets,
  }

  // Local States
  const [works, setWorks] = useState([...data.project.work.visual])
  const [current, setCurrent] = useState({current: 0, prev: 0});
  const [width, setWidth] = useState(9999);
  const [playerReady, setPlayerReady] = useState(false);
  // const [trackLoaded]

  const handleUpdateCurrent = (index) => {
    if (parseInt(index) >= 0) {
      // change to that index
      console.log(`${current.current} -> ${index}`);
      setCurrent(prev => ({current: index, prev: current.current}));
    } else {
      // increment by one
      let next = current.current + 1;
      if (next === works.length) {
        // We've reached the end so loop to the beginning
        next = 0;
      }
      setCurrent(prev => ({current: next, prev: current.current}));
    }
  }

  // Effects

  // Load track
  useEffect(()=>{
    
    let wasPlaying = _player.state.playing.value;
    if (wasPlaying) { 
      // And global playing was true
      // fade here then do rest after timeout
      player.fade('out', 1250);
      setTimeout(()=>{
        player.stop();
        console.log('Detail: setGlobal(false)');
        player.setGlobal(false);
        console.log('Detail: setTrack(id)');
        player.setTrack(id);
        player.current.sound.volume(0);
        console.log('Detail: play()');
        player.play();
        player.fade('in', 1000);
        _player.state.playing.setState(true);
        setPlayerReady(true);
      }, 0);
    } else {
      player.stop();
      player.setGlobal(false);
      player.setTrack(id);
      setPlayerReady(true);
    }

  }, []);
  
  // Listeners
  useEffect(()=>{
    setWidth(window.innerWidth);
    const handleResize = () => setWidth(window.innerWidth);
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    }
  }, []);

  let isMobile = false;
  if (width < 992) isMobile = true;

  const viewerProps = {
    current: current,
    setCurrent: setCurrent,
    works: works,
    setWorks: setWorks,
    handleUpdateCurrent: handleUpdateCurrent,
    width: width,
    artist: data.artists[0],
    assets: data.assets
  }

  const playerProps = {
    player: props.player,
    queue: props.queue,
    setQueue: props.setQueue,
    view: props.view,
    miniplayer: true,
    ready: playerReady,
  }

  return (
  <>
    <SEO title={`${id} • Gallery`} />
    <div className={styles.main}>
      <Info 
        data={data} 
        current={{index: current.current, data: works[current.current]}}
        setCurrent={handleUpdateCurrent}
        viewerProps={viewerProps}
        playerProps={playerProps}
        numWorks={works.length} 
        isMobile={isMobile}
      />
      {!isMobile && 
        <Viewer {...viewerProps} />
      }
    </div>
    <div id="credits" className={styles.credits}>
      {data.artists.map((artist, i) =>
        <Artist
          key={i}
          data={{
            id: data.project.artists[i],
            ...artist,
            color: data.project.color,
            assets: data.assets,
          }}
        />
      )}
    </div>
    <GalleryNav id={data.id} projects={Object.keys(projects)} />
  </>
  );
}

function Info(props) {
  const { 
    data, 
    current, 
    isMobile, 
    setCurrent,
    viewerProps,
    playerProps,
  } = props;

  const backgroundColor = { 
    style: {
      backgroundColor: data.project.color.bg,
      borderColor: data.project.color.bg,
    }
  }

  const color = {
    style: {
      color: data.project.color.fg,
    }
  }

  const visualArtist = (
    <Link to={`#${data.project.artists[0]}`} className={styles.bylineLink} focusClassName={styles.bylineLinkFocus} {...color}>
      {data.artists[0].name}
    </Link>
  )

  const audioArtist = (
    <Link to={`#${data.project.artists[1]}`} className={styles.bylineLink} focusClassName={styles.bylineLinkFocus} {...color}>
      {data.artists[1].name}
    </Link>
  )

  let switcherItems = [];
  for (let i = 0; i < props.numWorks; i++) {
    switcherItems.push(i+1);
  }
  

  return (
    <div className={styles.info}>
      {/* Intro */}
      <div className={classNames(styles.block, styles.intro)}>
        <Text h1 className={styles.title}>
          {data.project.work.visual[0].name}
        </Text>
        <div className={styles.byline}>
          by {visualArtist} x {audioArtist}
        </div>
        <Button to={`/catalog#${data.id}`} className={styles.readMore}>Read More</Button>
      </div>
      {/* Visual Artist */}
      <div className={classNames(styles.block, styles.visual)}>
        <div className={styles.name}>
          {current.data.name}
        </div>
        {current.data.medium && 
          <div className={styles.details}>{current.data.medium}</div>
        }
        {current.data.specs && 
          <div className={styles.details}>{current.data.specs}</div>
        }
        <div className={styles.switcher}>
          {switcherItems.map((item, i) =>
            <Button 
            key={item}
            onPress={() => setCurrent(i)} 
            className={classNames(
              styles.switcherBtn, 
              current.index == i ? styles.current : null
            )} 
            focusClassName={styles.switcherBtnFocus}
            disableDefaultClass
            >
              {item}
            </Button>
          )}
        </div>
        <Link to={data.artists[0].support} className={styles.btn} focusClassName={styles.btnFocus} disableDefaultClass external>
          Support Artist<Icon name="linkArrow" />
        </Link>
      </div>
      {/* Viewer (mobile) */}
      {isMobile && <Viewer {...viewerProps} />}
      {/* Player / Audio Artist */}
      <div className={classNames(styles.block, styles.audio, playerStyles.miniplayerWrapper)}>
        <Player {...playerProps} />
        <Link to={data.artists[1].support} className={styles.btn} focusClassName={styles.btnFocus} disableDefaultClass external>
          Support Artist<Icon name="linkArrow" />
        </Link>
      </div>
      {/* Credits Jump Link */}
      <ScrollLink  
        href="#credits"
        className={classNames(styles.block, styles.creditsBtn)} 
        to="credits"
        smooth={true} 
        duration={500}
        offset={-52}
        containerId="view"
        {...backgroundColor}
      >
        <Text h2>Credits<Icon name="chevronDown" /></Text>
      </ScrollLink>
    </div>
  );
}

function Viewer(props) {
  const { current, artist, handleUpdateCurrent, assets } = props;
  const [lastCurrent, setLastCurrent] = useState(current);
  const [works, setWorks] = useState([...props.works]);
  const [itemWidth, setItemWidth] = useState(); 
  const containerRef = useRef();
  const itemsRef = useRef([]);

  const handleClick = () => {
    handleUpdateCurrent();
  }

  useEffect(()=>{
    let item = itemsRef.current[0];
    if (!item) return;
    setItemWidth(item.getBoundingClientRect().width)
    console.log(itemWidth);
  }, [props.width]);

  let jumping = false;
  const isPrev = (current.prev > current.current);
  const isEnd = (current.current + 1 === works.length);
  const jumped = (Math.abs(current.current - current.prev) > 1);
  const isFirst = (current.current === 0);
  const looped = (isFirst && current.prev + 1 === works.length);

  if (jumped || isPrev) {
    if (!(isPrev && isFirst && isEnd) && !looped) {
      // We're jumping, not "nexting"
      // OR We're going backwards, so we also need to jump
      jumping = true;
      if (containerRef.current) {
        containerRef.current.classList.add(`${styles.jump}`);
      }
    }
  }

  if (!jumping) {
    if (containerRef.current) {
      containerRef.current.classList.remove(`${styles.jump}`);
    }
  }

  const currentWork = [works[current.current]];
  let nextIndex;

  let nextWork = [];
  if ((current.current + 1) === works.length) {
    nextWork[0] = works[0]; 
    nextIndex = 0
  } else {
    nextWork[0] = works[current.current + 1];
    nextIndex = current.current + 1;
  }

  const mainClasses = {
    enter: styles.mainSlideEnter,
    enterActive: styles.mainSlideEnterActive,
    enterDone: styles.mainSlideEnterDone,
    exit: styles.mainSlideExit,
    exitActive: styles.mainSlideExitActive,
  }
  const mainTimeout = {
    enter: 850,
    exit: 750,
  }

  const nextClasses = {
    appear: styles.nextSlideAppear,
    appearActive: styles.nextSlideAppearActive,
    appearDone: styles.nextSlideAppearDone,
    enter: styles.nextSlideEnter,
    enterActive: styles.nextSlideEnterActive,
    enterDone: styles.nextSlideEnterDone,
    exit: styles.nextSlideExit,
    exitActive: styles.nextSlideExitActive,
  }
  const nextTimeout = {
    appear: 1250,
    enter: 1000,
    exit: 850,
  }

  const path = `${assets.cdn}/works/${artist.id}`;

  const imageSrc = [
    `${path}-${current.current + 1}.jpg`,
    `${path}-${nextIndex + 1}.jpg`
  ];

  // const safeIndex = (index) => {
  //   if (index === works
  //   return
  // }

  return (
    <div className={styles.viewer}>
      <div className={styles.inner} ref={containerRef}>
        <div className={styles.current}>
          <TransitionGroup component={null}>
            {currentWork.map(item => 
              <CSSTransition 
                timeout={mainTimeout} 
                classNames={mainClasses}
                key={current.current}
              >
                <div 
                  key={`${item.name}`}
                  className={styles.item}
                >
                  <InnerImageZoom 
                    className={styles.zoomImg} 
                    src={imageSrc[0]} 
                    zoomSrc={imageSrc[0]}
                    alt={`Artwork: ${item.name} by ${artist.name}`} 
                    // zoomType="hover"
                    zoomScale={1.5}
                    zoomPreload
                    hideHint
                    hideCloseButton
                  />
                  {/* <img src={item.img} alt={`Artwork: ${item.name} by ${artist.name}`} /> */}
                </div>
              </CSSTransition>
            )}
          </TransitionGroup>
        </div>
        <div className={styles.next}>
          <TransitionGroup component={null}>
            {nextWork.map(item => 
              <CSSTransition 
                timeout={nextTimeout} 
                classNames={nextClasses}
                key={current.current}
                appear={true}
              >
                <div 
                  key={`${item.name}`}
                  className={styles.item}
                >
                  <Button 
                    disableDefaultClass 
                    className={styles.overlay} 
                    focusClassName={styles.overlayFocus}
                    onPress={handleUpdateCurrent}
                  >
                    <span className={styles.overlayInner}>
                      <span>Up<br /> Next</span>
                      <span>{item.name}</span>
                    </span>
                  </Button>
                  <img src={imageSrc[1]} alt={`Artwork: ${item.name} by ${artist.name}`} />
                </div>
              </CSSTransition>
            )}
          </TransitionGroup>
        </div>
      </div>
    </div>
  );
}

function Artist(props) {
  const { data } = props;

  let otherLinks = [...data.links];
  otherLinks.shift();
  const links = {
    main: data.links[0],
    other: otherLinks,
  }
  const color = { 
    style: {
      color: data.color.fg === 'default' ? '#cb1de2' : data.color.fg,
    }
  }

  const profileImage = `${data.assets.cdn}/profile/${data.id}.jpg`;

  return(
    <div id={data.id} className={styles.artist}>
      <div className={styles.header}>
        <div className={styles.left}>
          <img className={styles.image} src={profileImage} alt={data.name} /> 
        </div>
        <div className={styles.right}>
          <Text h2 className={styles.name}>{data.name}</Text>
          <Text className={styles.title}>{data.title}</Text>
          <Link to={links.main.url} className={styles.linkMain} external>{links.main.label}<Icon name="linkArrow" /></Link>
        </div>
      </div>
      
      <div className={styles.body}>
        <div className={styles.offset} />
        <div className={styles.content}>
          <div className={styles.bio}>
            {data.text.bio.map((text, i) => 
              <Text key={i}>{text}</Text>
            )}
          </div>
          <div className={styles.links}>
            {links.other.map(link =>
              <Link key={link.label} to={link.url} className={styles.link} {...color} external>
                {link.label}<Icon name="linkArrow" />
              </Link>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

function GalleryNav({ id, projects: keys }) {
  // Get keys of next and previous projects (for links as bottom)
  let index = keys.findIndex(key => key === id);

  let next;
  let prev;

  // If not last, update next index
  if (index === keys.length - 1) {
    next = keys[0];
  } else next = keys[index + 1];
  // If not first, update prev index
  if (index === 0) {
    prev = keys[keys.length - 1]
  } else prev = keys[index - 1];

  

  return(
    <div className={styles.galleryNav}>
      <Button to={`/gallery/${prev}`} className={styles.btn}>
        <Icon name="chevronLeft" /> Prev
      </Button>
      <Button to={`/gallery/#${id}`} className={styles.btn}>
        Return to Gallery
      </Button>
      <Button to={`/gallery/${next}`} className={styles.btn}> 
        Next <Icon name="chevronRight" />
      </Button>
    </div>
  );
}